body {
  font: 400 var(--font-size-root)/1.5 var(--font-primary);
  background: var(--color-dark-gray);
  color: var(--color-white);
}
a {
  transition: var(--transition);
  color: inherit;
}
img,
svg {
  max-width: 100%;
  vertical-align: top;
}
figure {
  margin: 0;
}
.admin-bar {
  --viewport-height: calc(100vh - var(--size-admin-bar));
}
.home .site {
  position: relative;
}
.site {
  min-height: var(--viewport-height);
}
.home .site-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}
.site-header {
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  color: var(--color-white);
}
.site-header a {
  text-decoration: none;
}
.site-branding {
  margin-right: auto;
  position: relative;
  z-index: 8;
}
.header-navigation {
  display: flex;
  align-items: center;
  gap: 2rem;
}
@media (max-width: 60rem) {
  .header-navigation {
    position: relative;
  }
}
.menu-mobile-opened {
  overflow-y: hidden;
  height: var(--viewport-height);
}
.menu-mobile-opened .menu-container {
  --opacity: 1;
  --visibility: visible;
  --transform: translateX(0);
  --pointer-events: inherit;
  transition: var(--transition);
}
.menu-container {
  display: flex;
  align-items: center;
  gap: 2rem;
}
@media (max-width: 60rem) {
  .menu-container {
    --opacity: 0;
    --visibility: hidden;
    --transform: translateX(0);
    --pointer-events: none;
    background: var(--color-primary);
    flex-direction: column;
    color: var(--color-white);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 6rem 2rem 2rem;
    text-align: center;
    transition: var(--transition);
    opacity: var(--opacity);
    visibility: var(--visibility);
    transform: var(--transform);
    pointer-events: var(--pointer-events);
  }
}
.primary-navigation,
.social-navigation {
  display: flex;
  gap: 1rem;
  margin: auto 0;
}
@media (max-width: 60rem) {
  .primary-navigation,
  .social-navigation {
    flex-direction: column;
    font-size: 1.5rem;
    text-transform: uppercase;
    font-weight: 200;
  }
}
.menu-button-container {
  position: relative;
  z-index: 10;
}
.menu-button-container .button {
  display: var(--menu-button-display);
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
  vertical-align: top;
  width: 42px;
  aspect-ratio: 1/1;
}
.menu-button-container .button svg {
  width: 100%;
  pointer-events: none;
}
.menu-button-container .button .line {
  fill: none;
  stroke: var(--color-white);
  stroke-width: 6;
  transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}
.menu-button-container .button .line1 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}
.menu-button-container .button .line2 {
  stroke-dasharray: 60 60;
  stroke-width: 6;
}
.menu-button-container .button .line3 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}
.menu-button-container .button.opened .line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6;
}
.menu-button-container .button.opened .line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30;
  stroke-width: 6;
}
.menu-button-container .button.opened .line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6;
}
.home .site-footer {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}
.site-footer {
  font-size: var(--font-size-xs);
  text-align: center;
  padding: 2rem 1rem 1rem;
  background: linear-gradient(to top, var(--color-dark-gray) 20%, transparent);
}
.site-footer a {
  text-decoration: none;
}
.site-footer a:hover {
  text-decoration: underline;
}
.footer-navigation {
  margin-bottom: 1rem;
  display: flex;
  justify-content: center;
  gap: 0.725em;
}
.footer-navigation > a + a::before {
  content: " • ";
  margin-right: 0.725em;
  display: inline-block;
}
.hero {
  min-height: var(--viewport-height);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 2fr 3fr;
  gap: 2rem;
}
.hero-picture {
  grid-area: 1/1/3/2;
  overflow: hidden;
  position: relative;
}
.hero-picture img {
  position: absolute;
  min-width: 100%;
  min-height: 100%;
  vertical-align: top;
  object-fit: cover;
}
.hero-title {
  position: relative;
  padding: 1rem 2rem;
  grid-area: 1/1/2/2;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  text-align: center;
}
.hero-title h1 {
  font: 400 var(--font-size-hero-title)/1 var(--font-hero), var(--font-secondary);
  margin: 0;
  position: relative;
  padding-top: 6rem;
}
.hero-title h1::after {
  position: absolute;
  top: 0;
  left: 0.25em;
  right: 0.25em;
  bottom: 0;
  z-index: -1;
}
.hero-title p {
  margin: 0;
  display: inline-block;
  padding: 0 1rem 0.5rem;
  font-size: var(--font-size-xl);
  line-height: 1;
}
.hero-color-white .hero-picture img {
  filter: brightness(0.5);
}
.hero-color-white h1,
.hero-color-white p {
  color: var(--color-white);
}
.hero-color-white h1 {
  text-shadow: 0 0 0.15em rgba(0,0,0,0.8), 0 0 5px rgba(0,0,0,0.5);
}
.hero-color-black h1,
.hero-color-black p {
  color: #010101;
}
.hero-color-black h1 {
  text-shadow: 0 0 30px rgba(255,255,255,0.5);
}
.hero-navigation {
  grid-area: 2/1/3/2;
  position: relative;
}
.categories-navigation {
  padding: 2rem 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem 3rem;
}
@media (max-width: 60rem) {
  .categories-navigation {
    flex-direction: column;
    align-items: center;
    gap: 0;
  }
}
.categories-navigation > a {
  font-size: var(--font-size-lg);
  font-weight: 200;
  text-transform: uppercase;
  text-decoration: none;
  display: flex;
  text-shadow: 0 0 5px var(--color-dark-gray);
  flex-direction: column;
  line-height: 1;
}
.categories-navigation > a::before,
.categories-navigation > a::after {
  --opacity: 0;
  content: "";
  background: var(--color-white);
  height: 1px;
  box-shadow: 0 0 5px var(--color-dark-gray);
  opacity: var(--opacity);
  transform: translateY(-0.075em);
  transform: var(--position);
  transition: var(--transition);
  transform-origin: var(--origin);
}
.categories-navigation > a::before {
  margin-bottom: 0.45em;
  --position: scaleX(0);
  --origin: 0 0;
}
.categories-navigation > a::after {
  margin-top: 0.45em;
  --position: scaleX(0);
  --origin: 100% 0;
}
.categories-navigation > a:hover::before {
  --opacity: 1;
  --position: scaleX(0.55);
}
.categories-navigation > a:hover::after {
  --opacity: 1;
  --position: scaleX(0.55);
}
.pictures-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.5rem;
}
.social-navigation {
  display: flex;
  gap: 1rem;
}
@media (max-width: 60rem) {
  .social-navigation {
    flex-direction: row;
    margin-bottom: 0;
  }
}
.social-navigation svg {
  width: 2em;
  vertical-align: top;
}
.picture {
  position: relative;
  border: 1px solid var(--color-white-50);
}
.picture img {
  vertical-align: top;
  width: 100%;
  height: auto;
}
.picture:nth-child(odd) .picture-title {
  text-align: right;
}
.picture-description {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 1rem;
}
.picture-title {
  margin: 0;
  font-weight: 200;
  line-height: 1;
}
